.slider {
  position: relative;
  width: 280px;
  height: 256px;
  --position: 50%;
}
@media screen and (min-width: 768px) {
  .slider {
    margin: 0 auto;
    width: 560px;
    height: 512px;
  }
}
@media screen and (min-width: 1280px) {
  .slider {
    grid-column: 2/3;
    grid-row: 1/4;
    align-self: center;
    margin-left: 47px;
    margin-bottom: 50px;
  }
}

.slider__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: left;
}

.slider__input {
  position: absolute;
  inset: 0;
  cursor: pointer;
  opacity: 0;
  width: 100%;
  height: 100%;
}

.slider__input:focus-visible ~ .slider__button {
  outline: 5px solid #000000;
  outline-offset: 1px;
}

.slider__line {
  position: absolute;
  width: 2px;
  height: 100%;
  background-color: #666666;
  left: var(--position);
  transform: translateX(-50%);
  pointer-events: none;
}

.slider__button {
  position: absolute;
  top: 50%;
  left: var(--position);
  transform: translate(-50%, -50%);
  pointer-events: none;
  border-radius: 100vw;
}

.slider__image-container-before {
  position: absolute;
  inset: 0;
  width: var(--position);
}

.slider__image-container-after {
  position: absolute;
  width: calc(100% - var(--position));
  height: 100%;
  right: 0;
}

.image-after {
  object-position: right;
}

.image-before {
  object-position: left;
}

